    min-height: 36px;
    height: 7vh;
    background: #fff;
}
@media (min-width: 450px) {
.chat{
        font-family: 'kakao';
        width: 330px;
        background-color:#a1c0d6;
}
.readMessage{
        height: 558px;
        padding-left:10px;
}
.sendMessage{
        height: 42px;
        background: #fff;
}
}
.chat > ul{
	position: relative;
	width: 330px;
	z-index: 0;
}
ul.from_me {
	text-align: right;
    padding-right:25px;
}
ul.from_me > li {
    display: inline-block;
	text-align: left;
    position: relative;
    background: #FCCE00;
	word-wrap: break-word;
    min-width:20%;
	max-width: 66%;
	padding:10px;
	margin-top: 10px;
	margin-bottom: 30px;
    border-radius: 10px;
}
ul.from_me > li:after {
    content: '';
    position: absolute;
    height: 25px;
    width: 25px;
    border-radius: 12.5px;
    z-index: -1;
    background: #a1c0d6;
    bottom: -10px;
    right: -13px;
}
ul.from_me > li:before {
    content: '';
    position: absolute;
    height: 25px;
    width: 25px;
    border-radius: 12.5px;
    z-index: -1;
    background: #FCCE00;
    bottom: -6px;
    right: -4px;
}
ul.from_me > li > span.nm {
	position: absolute;
	right:0px;
	top:-20px;
}
ul.from_me > li > span.tm {
	position: absolute;
	font-size: 14px;
	left:-65px;
	bottom:0px;
}
ul.from_you > li {
    display: inline-block;
    position: relative;    
	background: #FFFFFD;	
	word-wrap: break-word;
    min-width:20%;
	max-width: 66%;
	padding:10px;
	margin-top: 10px;
    border-radius: 10px;
}
ul.from_you > li:after {
    content: '';
    position: absolute;
    height: 25px;
    width: 25px;
    border-radius: 12.5px;
    z-index: -1;
    background: #a1c0d6;
    bottom: -10px;
    left: -13px;
}
ul.from_you > li:before {
    content: '';
    position: absolute;
    height: 25px;
    width: 25px;
    border-radius: 12.5px;
    z-index: -1;
    background: #FFFFFD;
    bottom: -6px;
    left: -4px;
}
ul.from_you {
	text-align: left;
    margin-top:20px;
}
ul.from_you > li > span.nm {
	position: absolute;
	left:0px;
	top:-20px;
}
ul.from_you > li > span.tm {
	position: absolute;
	font-size: 14px;
	right:-65px;
	bottom:0px;
}
